import React, { useState } from 'react';
import CreditCard from './creditCard/CreditCard';
import { useDispatch, useSelector } from 'react-redux';
import { checkConfirm, confirmEmail, orderActions, sendOrderData } from '../store/slices/orderSlice';
import { checkAuth } from '../store/slices/authSlice';
import { cartActions } from '../store/slices/cartSlice';

const EMPTY_USER = {
  name: '',
  lastName: '',
  phone: '',
  email: '',
};

const Order = ({ setIsOrder }) => {
  const dispatch = useDispatch();

  const { user: currentUser } = useSelector((state) => state.auth);
  const isEmailVerified = currentUser?.isVerified;

  const [isCorrect, setIsCorrect] = useState(false);
  const [user, setUser] = useState(EMPTY_USER);
  const orderData = useSelector((state) => state.order);

  const handleDataSubmit = (e) => {
    e.preventDefault();
    dispatch(orderActions.setUserData(user));
    setIsCorrect(true);
  };

  const handleChange = (e) => {
    setUser({ ...user, [e.target.name]: e.target.value });
  };

  const handleSendOrder = () => {
    setIsOrder(false);
    dispatch(
      sendOrderData({
        ...orderData,
        userData: {
          ...orderData.userData,
          email: currentUser?.login,
        },
      })
    );
    dispatch(orderActions.resetData());
    alert(' ');
  };

  return (
    <div className="cart-page__checkout checkout-cart">
      <div className="checkout-cart__wrapper">
        <h3 className="checkout-cart__title">   </h3>
        <form onSubmit={handleDataSubmit} action="" className="checkout-cart__form form-checkout">
          <div className="form-checkout__row">
            <div className="form-checkout__col">
              <label className="form-checkout__label">
                <span className="required">*</span>
              </label>
              <input //
                value={user.name}
                onChange={handleChange}
                required
                maxLength={100}
                name="name"
                type="text"
                placeholder=""
                className="input"
              />
            </div>
            <div className="form-checkout__col">
              <label className="form-checkout__label"></label>
              <input //
                value={user.lastName}
                onChange={handleChange}
                required
                maxLength={100}
                name="lastName"
                type="text"
                placeholder=""
                className="input"
              />
            </div>
          </div>
          <div className="form-checkout__row">
            <div className="form-checkout__col">
              <label className="form-checkout__label">
                 <span className="required">*</span>
              </label>
              <input
                value={user.number}
                onChange={handleChange}
                pattern="^\+375\d{9}$"
                required
                type="text"
                name="phone"
                placeholder="+375 (__) ___-__-__"
                className="input"
              />
            </div>
            <div className="form-checkout__col">
              <label className="form-checkout__label">Email</label>
              <input //
                value={isEmailVerified ? currentUser?.login : ' '}
                disabled
                onChange={handleChange}
                name="email"
                required
                type="email"
                maxLength={120}
                className={isEmailVerified ? 'input' : 'input  not-verify'}
                placeholder="example@mail.ru"
              />
            </div>
          </div>
          <button className="btn success">  </button>
        </form>
        {isCorrect && isEmailVerified && (
          <>
            <h3 className="checkout-cart__title">   </h3>
            <CreditCard />
          </>
        )}
        {orderData?.cardData && (
          <button style={{ marginTop: 20 }} className="btn success" onClick={handleSendOrder}>
             
          </button>
        )}
      </div>
    </div>
  );
};

export default Order;
